import React, {useState} from 'react';
import {connect} from 'react-redux';
import {Form, Input, Card, Col, Row, message, Upload, Button, Select, Badge} from 'antd';
import HeroComponents from "components/layout-components/CustomrPageHeaderAlt";
import ContainerTailwind from "components/layout-components/ContainerTailwind";
import ButtonPermission from "components/permission-components/button.permission";
import {LoadingOutlined, SaveOutlined, UploadOutlined} from "@ant-design/icons";
import ApiService from "../../../../../../../services/api.service";
import AudioTypeSelect from "../../../../general-views/screens/audio/components/forms/audio.type.select";
import AudioLanguageSelect from "../../../../general-views/screens/audio/components/forms/audio.language.select";
import Flex from "../../../../../../../components/shared-components/Flex";
import CustomIcon from "../../../../../../../components/util-components/CustomIcon";
import {ImageSvg} from "../../../../../../../assets/svg/icon";
import {findKey} from "../../../../../../../utils";
import {COLOR_DEFAULT_SELECTION} from "../../../../../../../configs/AppConfig";


const {Dragger} = Upload;

function mapStateToProps(state) {
	return {};
}

function Index() {
	const [loading, setLoading] = useState(false)
	const [form] = Form.useForm();
	const [fileName, setFileName] = useState(null)
	const [files, setFiles] = useState(null)
	const onFinish = () => {
		setLoading(true)
		form.validateFields()
			.then((value) => {
				let newUrl = `/api/v1/audio`
				let newValue = {}
				console.log({value})
				Object.entries(value).map(([key, value]) => {
					if (key === 'form_type') {
						newUrl += '/' + value
					} else {
						Reflect.set(newValue, key, value)
					}
				})
				const formdata = new FormData();
				Object.keys(newValue).forEach((key) => {
					if(key !== 'name'){
						formdata.append(key, newValue[key])
					}

				})
				formdata.append('file', files)
				
				new ApiService({
					url: newUrl,
					body: formdata
				}).post()
					.then((response) => {
						message.info(response?.message ?? "HEHEHE")
						setLoading(false)
						form.resetFields()
					})
					.catch((err) => {
						console.log({err})
						setLoading(false)
					})
				console.log({newValue, newUrl})
				setLoading(false)
			})
			.catch((err) => {
				setLoading(false)
			})
	}
	
	
	const imageUploadProps = {
		name: 'file',
		multiple: false,
		customRequest: async ({file, onSuccess}) => {
			console.log({file})
			setFileName(file?.originalName ?? file?.name ?? "-");
			setFiles(file)
			message.loading({content: "Loading...", key: "update-thumbnail", duration: 10})
			const src = await new Promise(resolve => {
				const reader = new FileReader();
				reader.readAsDataURL(file);
				reader.onload = () => resolve(reader.result);
			});
			message.success({content: "Successfully uploaded", key: "update-thumbnail", duration: 10})
			
			onSuccess('ok')
			
		},
		// onPreview: onPreview,
		showUploadList: false,
	}
	const beforeUploads = (file) => {
		const validate = file.type === 'audio/x-m4a' || file.type === 'audio/mpeg';
		if (!validate) {
			message.error('You can only upload WAV/MP3 file!');
		}
		const isLt2M = file.size / 1024 / 1024 < 10;
		if (!isLt2M) {
			message.error('Image must smaller than 10MB!');
		}
		return validate && isLt2M;
	}
	
	function onChangeName({target}){
		form.setFieldsValue({
			identifier: `${target?.value}`.replace(/ /g,'-').toLowerCase()
		})
	}
	
	return (
		<React.Fragment>
			<HeroComponents
				name={"Create"}
				positionName={['Master Data', 'Audio']}
				thumbnail={null}
				extra={<>
					<ButtonPermission
						permission={['admin']}
						theme={{
							type: "notification",
							property: {
								message: "Access Denied",
								description: "Anda tidak dapat menyimpan ke database "
							}
						}}
						config={{
							icon: <SaveOutlined/>,
							type: "primary",
							danger: false,
							loading,
							ghost: true,
							size: "small",
							onClick: onFinish,
						}}>
						<span>Save</span>
					</ButtonPermission>
				</>}
			/>
			<div style={{marginTop: 90}}>
				<ContainerTailwind>
					<Form
						form={form}
						layout={'vertical'}
					>
						<Row gutter={24}>
							<Col xs={{
								order: 2,
								span: 24
							}} lg={{
								order: 1,
								span: 14
							}}>
								<Card bordered={false}>
									<Form.Item label={'Identifier'} name={'identifier'} hidden
									           rules={[{required: true, message: "Can't be empty"}]}>
										<Input/>
									</Form.Item>
									<Form.Item label={'Display Name'} name={'name'}
									           rules={[{required: true, message: "Can't be empty"}]}>
										<Input onChange={onChangeName}/>
									</Form.Item>
									
									<AudioLanguageSelect
										select={{
											showSearch: true,
											onSelect: function (val) {
                        console.log({val})
											}
										}}
										name={'language_id'}
										label={'Language'}
										selectLabel={['data', 'name']}
										selectValue={['id']}
										rules={[{required: true, message: "Can't be empty!"}]}
									>
										{function ({item, index}) {
											return (
												<Select.Option value={findKey(item, ['id'])} key={findKey(item, ['id'])}>
													<Badge color={findKey(item, ['data', 'color']) ?? COLOR_DEFAULT_SELECTION}
													       text={findKey(item, ['data', 'name']) ?? "-"}/>
												</Select.Option>
											)
										}}
									</AudioLanguageSelect>
									<AudioTypeSelect
                    select={{
                      showSearch: false,
	                    autoClearSearchValue:true,
                      onSelect: function (val,args) {
                        form.setFieldsValue({
                          type_id:args?.id ?? null
                        })
                      },
	                    
                    }}
                    form={{
											
                    }}
										name={'type_id'} label={'Type'}
										selectLabel={['data', 'display_name']}
										selectValue={['id']}
										rules={[{required: true, message: "Can't be empty!"}]}
									>
										{function ({item, index}) {
											return (
												<Select.Option value={findKey(item, ['id']) ?? findKey(item, ['data','name']) ?? item?.id} id={item?.id} key={findKey(item, ['id'])}>
													<Badge color={findKey(item, ['data', 'color']) ?? COLOR_DEFAULT_SELECTION}
													       text={findKey(item, ['data', 'name']) ?? "-"}/>
												</Select.Option>
											)
										}}
									</AudioTypeSelect>
								</Card>
							</Col>
							<Col xs={{
								order: 1,
								span: 24
							}} lg={{
								order: 2,
								span: 10
							}}>
								<Card bordered={false}>
									<Flex alignItems={'center'} mobileFlex={true}
									      className={'yid-img-uploads mt-4'}>
										<Dragger {...imageUploadProps} beforeUpload={beforeUploads}>
											<div>
												{
													fileName ?
														<div>
															<LoadingOutlined className="font-size-xxl text-primary"/>
															<div className="mt-3">{fileName}</div>
														</div>
														:
														<div>
															<CustomIcon className="display-3" svg={ImageSvg}/>
															<p>Click or drag file to upload</p>
														</div>
												}
											</div>
										</Dragger>
										{/*<Upload {...imageUploadProps} multiple={false}*/}
										{/*        beforeUpload={beforeUploads}>*/}
										{/*    <div className={'w-100 border'} style={{width:"100%"}}>*/}
										{/*        <Button*/}
										{/*            icon={<UploadOutlined/>}*/}
										{/*            type={'primary'}*/}
										{/*            size={'small'}*/}
										{/*            style={{width: '100%'}}>Click to Upload</Button>*/}
										{/*    </div>*/}
										{/*</Upload>*/}
									</Flex>
								</Card>
							</Col>
						</Row>
					</Form>
				</ContainerTailwind>
			</div>
		</React.Fragment>
	);
}

export default connect(
	mapStateToProps,
)(Index);